import React from "react";
import DataGrid from "../../containers/DataGrid";
import ReactHtmlParser from "react-html-parser";
import { Anchor } from "grommet";
import { format, parseISO } from "date-fns";

const App = ({ id }) => {
  const configureCol = [
    {
      property: "objectId",
      headerText: "",
      primaryKey: true,
      render: (data) => <> </>,
    },
    {
      property: "name",
      headerText: "Name",
    },
    {
      property: "Description",
      headerText: "Info",
      type: "Textarea",
      render: (data) => <> {ReactHtmlParser(data.description)} </>,
    },
    {
      property: "bidEndAt",
      headerText: "BidEnd",
      type: "DateTimePicker",
      render: (data) => (
        <>
          {" "}
          {data.bidEndAt
            ? format(parseISO(data.bidEndAt.iso), "yyyy MMM dd HH:mm:s")
            : "Not Set"}{" "}
        </>
      ),
    },
    {
      property: "available",
      headerText: "Show",
      type: "CheckBox",
      render: (data) => <> {data.available ? "Yes" : "No"} </>,
    },
    {
      property: "startBid",
      headerText: "startBid",
      render: (data) => <> {data.startBid} </>,
    },
    {
      headerText: "Image",
      render: (data) => (
        <>
          {" "}
          {data.photo ? (
            <Anchor href={data.photo.url}> View </Anchor>
          ) : (
            "Empty"
          )}{" "}
        </>
      ),
    },
  ];
  return (
    <>
      <DataGrid
        suffix=""
        objectClass="Product"
        columns={configureCol}
        query={(q) => q}
        fetchData={{
          action: "searchProduct",
          params: {
            query: {
              storeId: {
                f: "equalTo",
                v: id,
              },
            },
          },
        }}
      />
    </>
  );
};
export default App;
